<template>
    <div class="about">
        <img :src="baseInfo.banner" class="img"/>
        <div class="navigation" v-if="baseInfo.title !== baseInfo.home.title">
            <img src="../../assets/home.png" class="home"/>
            <div class="m-l-7 pointer" @click="navigator">{{ baseInfo.home.title }}</div>
            <div class="m-l-7"> > </div>
            <div class="m-l-7">{{ baseInfo.title }}</div>
        </div>
        <div class="news">
            <div class="company-news">{{ baseInfo.title }}</div>
            <div class="company-en">PRODUCTS</div>
            <div class="rows">
                <div class="classification">
                    <div class="classification-title">{{ baseInfo.title }}</div>
                    <div class="classification-item"
                        :class="{ 'classification-item-check': productIndex === index }"
                        v-for="(item,index) in info.products"
                        :key="index"
                        @click="productIndex = index">
                        {{ item.category }}
                    </div>
                </div>
                <div class="pro-content">
                    <div class="pro-content-c" v-if="info.products">
                        <div @click="toDetaul(index)" v-for="(item,index) in info.products[productIndex].items" class="mouse" :key="index">
                            <div class="pri-imgs">
                                <img :src="item.product_image_url"/>
                            </div>
                            <div class="pro-item-name">{{ item.product_name }}</div>
                            <div class="shade">
                                <div class="between">
                                    <img src="../../assets/zs.png"/>
                                    <img src="../../assets/right.png" style="width: 20px;height: 12px;"/>
                                </div>
                                <div class="shade-title">{{ item.product_name }}</div>
                                <div class="shade-content">{{ $t(item.product_description) }}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { useRouter, useRoute } from "vue-router";
import { useStore } from "vuex";
import { computed, ref } from 'vue'
    const store = useStore();
    const router = useRouter();
    const route = useRoute();
    const info = computed(() => store.state.info);
    const productIndex = ref(0)
    const baseInfo = computed(() => {
        if (store.state.info.navigation) {
            let obj = store.state.info.navigation.find(el => el.router === route.path)
            document.title = `${ obj.title }-${ info.value.company_name }`
            return {
                banner: obj.banner,
                title: obj.title,
                home: store.state.info.navigation[0]
            }
        }
        return {
            banner: '',
            title: '',
            home: {}
        }
    });
    const navigator = () =>{
        router.push(baseInfo.value.home.router)
    }
    const toDetaul = (index) => {
        router.push({
            path: '/productDetail',
            query:{
                index,
                productIndex: productIndex.value
            }
        })
    }


</script>
